      userName: '终究是梦丶',
      comment:
        '说实话当看到这个电影封面时，我原本以为会是一部不折不扣的美国科幻大片。当看到故事开头那两句话是，他仿佛是个男孩可还年少！这句话很有意思。在仔细深入看时发现他的生活是不幸的，父母离婚，他本人没有像其他孩子一样有一颗开朗的心，就如同他在学校受欺负一样他选择了沉默说明他本身内心孤单的，就如同我们童年生活中或多或少有些这样的遭遇，比如父母离婚，或者成为留守儿童。在深入电影当中树人讲的第一个故事本身仿佛也是外述说主人公的遭遇。在进入到他爸来见面的时候他是那么的开心又有些恨，他对话他爸时候说了一句你会接我去吗？仔细回想电影的开始他也就是个孩子他真的需要爱。电影跟她外婆的桥段时，就如同大多数留守儿童一样跟着不是自己的父母那种感受，我相信大多数人都能感受到那种心情。到故事中段讲第2个故事时，其实故事本身是主人公的内心设计的比如故事讲到那树做的药可以救活她的母亲。其实大家看的时候以为真的奇迹会发生，其实那真的不可能，这个只是主人公内心的渴望。故事末端他看着外婆看他以前得录像时，他知道还是有人爱着他。再到学校爆发打伤他欺负他的人。其实都是他人身内心世界的一种变化。再到在见他母亲时他母亲说我真的希望我可以活一百年，看到这个时候我有些忍不住眼睛有些忍不住。再到他去面对树人事勇敢得说出第4个故事，接受她母亲的死亡。这就是在讲述一个小孩子内心世界的一种变化。这部片子真的很有寓意，其实树人根本就不存在，这只是他内心世界的心理路程。我想大多数都经历过一些你无能为力的困难，或者说亲人离去，或者说在社会中万分坎坷，到到最后你都要勇敢得接受去面对。很好的一部片子。'
